.m-content .banner {
  height: 45.0rem;
  text-align: center;
  padding: 18.0rem 0 5.0rem;
  background-color: #80DEEA;
}
.m-content .banner .product-name {
  color: #014B62;
  font-weight: 400;
  height: 6.0rem;
  line-height: 6.0rem;
  font-size: 4.8rem;
}
.m-content .banner .product-brief {
  font-size: 1.8rem;
  height: 3.0rem;
  line-height: 3.0rem;
  color: #333;
}
.m-content .banner .actions {
  margin-top: 8.0rem;
}
.m-content .banner .actions span {
  display: inline-block;
  color: #333;
  cursor: pointer;
  font-size: 1.8rem;
  height: 4.8rem;
  line-height: 4.8rem;
  padding: 0 4.0rem;
  border: 0.1rem solid #e5e5e5;
  border-radius: 1.0rem;
  margin: 0 0.5rem;
}
.m-content .brief-block {
  width: 114rem;
  margin: 0 auto;
}
.m-content .brief-block::after {
  content: "";
  display: block;
  height: 0;
  width: 100%;
  clear: both;
}
.m-content .brief-block .item {
  width: 38rem;
  padding: 2.0rem 5.0rem;
  float: left;
}
.fullpage-menu {
  position: fixed;
  right: 3.0rem;
  top: 50%;
  margin-top: -3rem;
}
.fullpage-menu li {
  width: 1.0rem;
  height: 1.0rem;
  border-radius: 1.0rem;
  background: #fff;
  margin: 0.5rem 0;
}
.fullpage-menu li.active {
  background-color: #ccc;
}



